// src/components/LocationPicker.jsx
import React, { useState, useMemo, useEffect } from "react";
import { GoogleMap, useLoadScript, Marker } from "@react-google-maps/api";
import usePlacesAutocomplete, {
  getGeocode,
  getLatLng,
} from "use-places-autocomplete";
import { Input } from "@/components/ui/input";
import { Card } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Loader2, MapPin, LocateFixed } from "lucide-react";

const libraries = ["places"];

function LocateButton({ onLocate, isSearching }) {
    return (
        <Button
            type="button"
            variant="outline"
            size="sm"
            onClick={onLocate}
            disabled={isSearching}
            className="flex items-center gap-1.5"
        >
            {isSearching ? (
                <Loader2 className="animate-spin h-4 w-4" />
            ) : (
                <LocateFixed className="h-4 w-4" />
            )}
            {isSearching ? "Locating..." : "Use Current Location"}
        </Button>
    );
}

export default function LocationPicker({ onLocationSelect, initialLocation }) {
  const { isLoaded } = useLoadScript({
    googleMapsApiKey: process.env.NEXT_PUBLIC_GOOGLE_MAPS_API_KEY,
    libraries: libraries,
  });

  // Default center (e.g. India)
  const defaultCenter = useMemo(() => ({ lat: 21.1458, lng: 79.0882 }), []);
  const [selected, setSelected] = useState(initialLocation || null);
  const [isLocating, setIsLocating] = useState(false);

  // --- UPDATED: Geolocation Function with High Accuracy ---
  const locateUser = () => {
    if (!navigator.geolocation) {
      alert("Geolocation is not supported by your browser.");
      return;
    }
    setIsLocating(true);

    navigator.geolocation.getCurrentPosition(
      async (position) => {
        const { latitude, longitude } = position.coords;
        const userLocation = { lat: latitude, lng: longitude };

        try {
          const results = await getGeocode({ location: userLocation });
          const address = results[0]?.formatted_address || "Current Device Location";
          
          setSelected(userLocation);
          onLocationSelect({ 
              address, 
              ...userLocation,
              components: results[0]?.address_components || []
          }); 
        } catch (error) {
           console.error("Reverse Geocoding failed:", error);
           setSelected(userLocation);
           onLocationSelect({ address: "Current Location (Coords Only)", ...userLocation });
        } finally {
            setIsLocating(false);
        }
      },
      (error) => {
        console.warn('Geolocation failed:', error);
        setIsLocating(false);
        // Fallback to IP-based location if high accuracy fails
        alert("Could not get precise location. Please search for your address manually.");
      },
      // --- THIS IS THE FIX ---
      {
        enableHighAccuracy: true, // Forces GPS/Wi-Fi triangulation
        timeout: 10000,           // Wait up to 10s for a good lock
        maximumAge: 0             // Do not use cached positions
      }
    );
  };

  // Auto-locate on load if no initial data
  useEffect(() => {
    if (!isLoaded || (initialLocation && initialLocation.lat)) return;
    // Uncomment the line below if you want it to auto-locate without clicking
    // locateUser(); 
  }, [isLoaded, initialLocation]);

  if (!isLoaded) return <div className="p-4 flex items-center gap-2 text-muted-foreground"><Loader2 className="animate-spin h-4 w-4" /> Loading Maps...</div>;

  return (
    <div className="space-y-4">
      <div className="relative">
        <PlacesAutocomplete setSelected={setSelected} onLocationSelect={onLocationSelect} />
      </div>
      
      <div className="flex justify-end">
          <LocateButton onLocate={locateUser} isSearching={isLocating} />
      </div>

      <div className="h-[300px] w-full rounded-md overflow-hidden border relative">
        <GoogleMap
          zoom={selected ? 15 : 5}
          center={selected || defaultCenter}
          mapContainerClassName="w-full h-full"
          onClick={(e) => {
            const lat = e.latLng.lat();
            const lng = e.latLng.lng();
            const newLoc = { lat, lng };
            setSelected(newLoc);
            
            // Optional: Reverse geocode on click to get address text
            // For now we pass coordinates, parent handles submission
            onLocationSelect({ 
                address: "Pinned Location", 
                ...newLoc 
            }); 
          }}
        >
          {selected && <Marker position={selected} />}
        </GoogleMap>
        {!selected && (
             <div className="absolute inset-0 flex items-center justify-center bg-black/5 pointer-events-none">
                <span className="bg-white/80 px-3 py-1 rounded-md text-sm text-gray-600 shadow-sm">
                    Search, click map, or use 'Current Location'
                </span>
             </div>
        )}
      </div>
    </div>
  );
}

function PlacesAutocomplete({ setSelected, onLocationSelect }) {
  const {
    ready,
    value,
    setValue,
    suggestions: { status, data },
    clearSuggestions,
  } = usePlacesAutocomplete();

  const handleSelect = async (address) => {
    setValue(address, false);
    clearSuggestions();

    try {
      const results = await getGeocode({ address });
      const { lat, lng } = await getLatLng(results[0]);
      
      const locationData = {
        address,
        lat,
        lng,
        components: results[0].address_components 
      };
      
      setSelected({ lat, lng });
      onLocationSelect(locationData);
    } catch (error) {
      console.error("Error: ", error);
    }
  };

  return (
    <div className="relative z-10">
      <div className="flex items-center border rounded-md px-3 bg-white">
        <MapPin className="h-4 w-4 text-gray-500 mr-2" />
        <Input
          value={value}
          onChange={(e) => setValue(e.target.value)}
          disabled={!ready}
          className="border-0 shadow-none focus-visible:ring-0 px-0"
          placeholder="Search for your shop address..."
        />
      </div>
      
      {status === "OK" && (
        <Card className="absolute top-full mt-1 w-full bg-white shadow-lg rounded-md overflow-hidden">
          <ul className="divide-y">
            {data.map(({ place_id, description }) => (
              <li
                key={place_id}
                onClick={() => handleSelect(description)}
                className="p-3 hover:bg-gray-50 cursor-pointer text-sm"
              >
                {description}
              </li>
            ))}
          </ul>
        </Card>
      )}
    </div>
  );
}